let url
let login = false
// if (process.env['REACT_APP_BASE_URL']) 
url = `${process.env['REACT_APP_BASE_URL']}`


export const Auth = async (history,PayLoadLogin) => await fetch(`${url}/Authenticate`,
  {
    method: 'POST',
    headers: new Headers({
      'content-type': 'application/json'
    }),
    body: PayLoadLogin
  })
  .then(x => {
    if (x.status === 200) return x.json()
    if (x.status === 401) history.push("/Login")
    return
  })
  .then(x => {
    if (x) {
      login = x.user
      history.push("/"); 
      // history.push("/")
      return x.user
    }
    })


export const fullpage_index = (idPagina) => fetch(`${url}/GetPaginas`, {
  method: 'post',
  body: idPagina,
  headers: new Headers({
    'content-type': 'application/json',
    'Authorization': 'Bearer ' + login,
  })
})
  .then(x => x.json());


  export const GetPontos = (latLong,history) => fetch(`${url}/Ponto/GetPontos`, {
    method: 'post',
    body: JSON.stringify(latLong),
    headers: new Headers({
      'content-type': 'application/json',
      'Authorization': 'Bearer ' + login,
    })
  })
  .then(x => {
    if (x.status === 200) return x.json()
    if (x.status === 401) history.push("/Login")
    return
  })
  

  


  export const PostPonto = (ponto) => fetch(`${url}/Ponto/PostPonto`, {
    method: 'post',
    body: JSON.stringify(ponto),
    headers: new Headers({
      'content-type': 'application/json',
      'Authorization': 'Bearer ' + login,
    })
  }).then(x => x.text())
  .then((x) =>console.log(x))
  
  